import { Card } from './card';

// 
export enum PlayerStatus {
  WAITING = 'waiting',          // 
  PLAYING = 'playing',          // 
  FINISHED = 'finished',        // 
  DISCONNECTED = 'disconnected' // 
}

// 
export enum PlayerType {
  HUMAN = 'human',      // 
  AI = 'ai'             // AI
}

// 
export interface Player {
  id: string;
  name: string;
  type: PlayerType;
  status: PlayerStatus;
  cards: Card[];              // 
  position: number;           // 0-3
  score: number;              // 
  isLandlord: boolean;        // 
  totalGames: number;         // 
  wins: number;               // 
}

// 
export enum PlayerAction {
  PASS = 'pass',              // 
  PLAY_CARDS = 'play_cards',  // 
  CALL_LANDLORD = 'call_landlord',  // 
  GRAB_LANDLORD = 'grab_landlord'   // 
}

// 
export interface PlayerMove {
  playerId: string;
  action: PlayerAction;
  cards?: Card[];             // 
  timestamp: number;          // 
}

// 
export function createPlayer(
  id: string,
  name: string,
  type: PlayerType,
  position: number
): Player {
  return {
    id,
    name,
    type,
    status: PlayerStatus.WAITING,
    cards: [],
    position,
    score: 0,
    isLandlord: false,
    totalGames: 0,
    wins: 0
  };
}

// 
export interface PlayerStats {
  playerId: string;
  playerName: string;
  totalGames: number;
  wins: number;
  losses: number;
  winRate: number;
  totalScore: number;
  averageScore: number;
}

// 
export function calculateWinRate(wins: number, totalGames: number): number {
  if (totalGames === 0) return 0;
  return Math.round((wins / totalGames) * 100 * 100) / 100; // 
} 